.logo {
  position: relative;
  bottom: -8px;
  height: 80px;
}

.image {
  &__header {
    &--background {
      background-image: url("/assets/images/headerfoto-Schuytse-Tandartsen-Arnhem.jpg");
      background-position: center;
      background-size: cover;
      overflow: hidden;
    }
  }

  &--index {
    position: absolute;
    top: 5rem;
    right: -12rem;
    height: 44rem;
  }

  &--usp {
    height: 14rem;
  }
}

svg path {
  position: relative;
  top: -1rem;
  z-index: 99;
  stroke: none;

  &.grey {
    fill: $color-tertiary;
  }

  &.white {
    fill: $color-white;
  }
}
