// FLEX HELPERS
.flex {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

  @media screen and (min-width: 48em) {
    flex-direction: row;
    flex-wrap: nowrap;
  }
}

.flex-column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.space-between {
  justify-content: space-between;
}

.space-around {
  justify-content: space-around;
}

